<template>
  <div>
    <layout-box one="菜单列表">
      <el-card shadow="never">
        <div class="d-flex">
          <span class="flex-1">菜单列表</span>
          <el-button
            class="ml-2"
            size="mini"
            @click="addDialogVisible = true"
          >
            添加菜单</el-button>
        </div>
      </el-card>
      <div class="mt-1">
        <el-table v-loading="listLoading" border :data="list" style="width: 100%">
          <el-table-column label="编号" align="center">
            <template slot-scope="scope">{{ scope.row.menuCode }}</template>
          </el-table-column>
          <el-table-column
            prop="menuName"
            label="菜单名称"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="menuUrl"
            label="菜单路径"
            align="center"
          >
          </el-table-column>
          <el-table-column label="前端图标" align="center">
            <template slot-scope="scope">
              <i :class="scope.row.menuIcon"> </i>
            </template>
          </el-table-column>
          <el-table-column label="菜单级数" align="center">
            <template slot-scope="scope">
              {{ scope.row.parentId | levelFilter }}
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="添加时间" align="center">
          </el-table-column>
          <el-table-column label="菜单类型" align="center">
            <template slot-scope="scope">
              {{ scope.row.menuType | formatType }}
            </template>
          </el-table-column>
          <el-table-column
            prop="menuSort"
            label="排序"
            align="center"
          >
          </el-table-column>
          <el-table-column label="设置" align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="mini"
                :disabled="scope.row.parentId | disableNextLevel"
                @click="handleShowNextLevel(scope.row.id)"
              >查看下级</el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="110" align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="text"
                @click="showEditDialog(scope.row.id)"
              >编辑
              </el-button>
              <el-button
                size="mini"
                type="text"
                @click="handleDelete(scope.row.id)"
              >删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--        <tree-table :data="list"  :columns="columns"></tree-table>-->
        <!-- 添加菜单的对话框 -->
        <el-dialog title="添加菜单" width="40%" :close-on-click-modal="false" :visible.sync="addDialogVisible" @close="addDialogClosed">
          <!-- 内容主体区域 -->
          <el-form
            ref="addFormRef"
            label-width="100px"
            :model="resource"
            :rules="addFormRules"
          >
            <el-form-item label="菜单名称：" prop="menuName">
              <el-input v-model="resource.menuName"> </el-input>
            </el-form-item>
            <el-form-item label="菜单类型" prop="menuType">
              <el-select v-model="resource.menuType" placeholder="请选择类型">
                <el-option
                  v-for="items in optionsfrom"
                  :key="items.value"
                  :label="items.label"
                  :value="items.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上级菜单" prop="parentId">
              <el-cascader v-model="resource.parentId" :options="menuTreeMenu" :props="{ checkStrictly: true }" clearable @change="(e) => onSelectMenu(e, 'resource')"></el-cascader>
            </el-form-item>
            <el-form-item label="菜单路径：" prop="menuUrl">
              <el-input v-model="resource.menuUrl"></el-input>
            </el-form-item>
            <el-form-item label="前端图标：" prop="menuIcon">
              <el-input v-model="resource.menuIcon"></el-input>
              <i style="margin-left: 8px" :icon-class="resource.menuIcon"></i>
            </el-form-item>
            <el-form-item label="编号" prop="menuCode">
              <el-input v-model="resource.menuCode"></el-input>
            </el-form-item>
            <el-form-item label="排序" prop="menuSort">
              <el-input v-model="resource.menuSort"></el-input>
            </el-form-item>
            <el-form-item label="权限" prop="permission">
              <el-input v-model="resource.permission"></el-input>
            </el-form-item>
          </el-form>
          <!-- 底部区域 -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addUser">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 编辑 -->
        <el-dialog
          title="编辑菜单"
          width="40%"
          :visible.sync="editDialogVisible"
          @close="editDialogClosed"
        >
          <!-- 内容主体区域 -->
          <el-form
            ref="editFormRef"
            label-width="100px"
            :model="editForm"
            :rules="editFormRules"
          >
            <el-form-item label="菜单类型" prop="menuType">
              <el-select v-model="editForm.menuType" placeholder="请选择类型">
                <el-option
                  v-for="item in editoptios"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上级菜单">
              <el-cascader v-model="editForm.parentId" :options="menuTreeMenu" :props="{ checkStrictly: true }" clearable @change="(e) => onSelectMenu(e, 'editForm')"></el-cascader>
            </el-form-item>
            <el-form-item label="菜单名称：" prop="menuName">
              <el-input v-model="editForm.menuName"> </el-input>
            </el-form-item>
            <el-form-item label="菜单路径：">
              <el-input v-model="editForm.menuUrl"></el-input>
            </el-form-item>
            <el-form-item label="前端图标">
              <el-input v-model="editForm.menuIcon" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="编号：" prop="menuCode">
              <el-input v-model="editForm.menuCode"></el-input>
            </el-form-item>
            <el-form-item label="排序：" prop="menuSort">
              <el-input v-model="editForm.menuSort"></el-input>
            </el-form-item>
          </el-form>
          <!-- 底部区域 -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="editUserInfo">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <el-pagination
        class="mt-1"
        background
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="queryInfo.pageNo"
        :page-size="queryInfo.pageSize"
        :page-sizes="[2, 5, 10]"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </layout-box>
  </div>
</template>
<script>
const defaultTypeOptions = [
  {
    label: '菜单',
    value: 0
  },
  {
    label: '按钮',
    value: 1
  },
]
export default {
  data() {
    return {
      listLoading: true,
      optionsfrom: [
        {
          label: '菜单',
          value: 0
        },
        {
          label: '按钮',
          value: 1
        }
      ],
      options: [],
      addFormRules: {
        menuName: [{ required: true, message: '请输入姓名', trigger: 'change' }],
        menuCode: [{ required: true, message: '请输入编码', trigger: 'blur' }],
        menuSort: [{ required: true, message: '请输入排序', trigger: 'blur' }],
        menuType: [{ required: true, message: '请输入菜单类型', trigger: 'change' }]
      },
      editFormRules: {
        menuType: [{ required: true, message: '请输入菜单类型', trigger: 'change' }],
        menuName: [{ required: true, message: '请输入姓名', trigger: 'blur' }],
        menuCode: [{ required: true, message: '请输入编码', trigger: 'blur' }],
        menuSort: [{ required: true, message: '请输入排序', trigger: 'blur' }]
      },
      queryInfo: {
        id: '',
        pageNo: 1,
        pageSize: 10
      },
      list: [],
      editForm: {
        menuTyp: ''
      },
      editoptios: [
        {
          value: 0,
          label: '菜单'
        },
        {
          value: 1,
          label: '按钮'
        }
      ],
      total: 0,
      addDialogVisible: false,
      editDialogVisible: false,
      resource: {
        roleName: '',
        roleCode: '',
        menuSor: '',
        menuUrl: '',
        menuType: '',
        menuIcon: ''
      },
      menuTreeMenu: {}, // 树形菜单
    }
  },
  filters: {
    formatType(menuType) {
      for (let i = 0; i < defaultTypeOptions.length; i++) {
        if (menuType === defaultTypeOptions[i].value) {
          return defaultTypeOptions[i].label
        }
      }
      return ''
    },
    levelFilter(parentId) {
      if (parentId === '0') {
        return '一级'
      } else if (parentId !== '0') {
        return '二级'
      }
    },
    disableNextLevel(parentId) {
      if (parentId === '0') {
        return false
      } else {
        return true
      }
    }
  },

  methods: {
    // 查看下级
    async handleShowNextLevel(id) {
      const res = await this.$http.post(
        '/woho/sys/menu/findByParentId/' + id
      )
      console.log(res)
      this.list = res.data.data
    },
    async fetch() {
      this.listLoading = true
      const res = await this.$http.post('/woho/sys/menu/query', this.queryInfo)
      this.listLoading = false
      this.list = res.data.data.list
      this.total = res.data.data.total
    },
    // 添加菜单
    async addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('资料填写完整')
        const res = await this.$http.post(
          '/woho/sys/menu/insert',
          this.resource
        )
        if (res.data.code !== 200) return
        this.$message.success('添加菜单成功！')
        // 隐藏添加用户的对话框
        this.addDialogVisible = false
        // 重新获取用户列表数据
        this.fetch()
        this.apiMenuTreeMenu()
      })
    },
    // 删除菜单
    async handleDelete(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该菜单, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const res = await this.$http.delete('/woho/sys/menu/delete/' + id)
      // console.log(res)
      if (res.data.code !== 200) {
        return this.$message.error('删除菜单失败！')
      }
      this.$message.success('删除菜单成功！')
      this.fetch()
    },
    // 编辑菜单
    async showEditDialog(id) {
      const res = await this.$http.post('/woho/sys/menu/findById/' + id)
      // console.log(res)
      this.editForm = res.data
      console.log(this.editForm)
      this.editDialogVisible = true
    },
    // 修改信息并提交
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请填写完整')
        const res = await this.$http.put('/woho/sys/menu/update', this.editForm)
        console.log(res)
        if (res.data.code !== 200) {
          return this.$message.error('更新菜单失败！')
        }
        // 关闭对话框
        this.editDialogVisible = false
        // 提示修改成功
        this.$message.success('更改菜单成功')
        //  刷新列表数据
        this.fetch()
      })
    },
    // 修改用户对话框关闭事件
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },

    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.fetch()
    },
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pageNo = newPage
      this.fetch()
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },

    // 选择上级菜单
    onSelectMenu(e, fileName) {
      this[fileName].parentId = e[e.length - 1]
    },

    // 私有方法-----------------------------------------------------
    // 递归处理获取的树形菜单
    _recursiveChildList (childList) {
      if (childList.length > 0) {
        var children = childList.map((item) => {
          item.value = item.id
          item.label = item.menuName
          item.children = this._recursiveChildList(item.childList)
          return item
        })
      }
      return children
    },

    // api--------------------------------------------
    // 树形菜单
    apiMenuTreeMenu () {
      this.$http.post('/woho/sys/menu/treeMenu')
        .then((res) => {
          if (res.data.success) {
            this.menuTreeMenu = this._recursiveChildList(res.data.data)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  },

  created() {
    this.fetch()
    this.apiMenuTreeMenu()
  },
}
</script>

<style></style>
